.loader {
    border: 16px solid #f3f3f3;
    border-top: 16px solid #605ebf;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    margin: 16px;
    animation: spin 2s linear infinite;
}

.loader::after {
    content: "";
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    border-radius: 50%;
    box-shadow: inset 0px 0px 20px #605ebf;
}
  
@keyframes spin {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
}