* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body, input, button {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
}

#root {
  position: relative;
  min-height: 100vh;
}

h1, h2 {
  color: white;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}